.footer {
    background-color: $background-color;
    &__menu {
        border-top: 2px solid #dedede;
        img {
            height: 50px;
            width: 150px;
            margin-bottom: 10px;
        }
        p {
            font-size: 14px;
            line-height: 22px;
            color: $text-color;
        }
        padding: 50px 0 0;
        h4 {
            font-size: 24px;
            line-height: 32px;
            font-weight: bold;
            padding-bottom: 10px;
            color: $primary-color;
        }
        ul {
            padding: 0;
            li {
                text-decoration: none;
                list-style: none;
                text-align: left;
                padding: 0;
                a {
                    color: $text-color;
                    font-size: 14px;
                    line-height: 22px;
                    &:hover {
                        text-decoration: none;
                        color: $primary-color;
                        transition: all .2s ease;
                    }
                }
            }
        }
    }
    &__copyright {
        .info {
            border-top: 2px solid #dedede;
            padding-top: 12px;
            p {
                color: $text-color;
                font-size: 14px;
                line-height: 22px;
            }
        }
    }
}

.call,
.location {
    .svg {
        height: 12px;
        width: 12px;
        margin-right: 5px;
        margin-top: 5px;
    }
    p {
        display: inline;
        font-size: 14px;
        line-height: 22px;
        color: $text-color;
    }
}

.social-media {
    float: right;
    display: inline-flex;
    margin-top: 20px;
    i {
        margin-left: 10px;
        cursor: pointer;
        &:hover {
            color: $primary-color;
            transition: all .2s ease;
        }
    }
}