* {
    outline: 0;
}

.wrapper {
    font-size: 12px;
    line-height: 20px;
    min-height: 100vh;
    .main-content {
        min-height: 100vh;
        padding: 10px 20px;
        .username {
            float: right;
            margin: 0 20px;
            color: $text-color;
            .dropdown {
                background-color: transparent;
                box-shadow: none;
            }
        }
        .card {
            border: 0;
            background-color: transparent;
            margin: 50px auto;
            h3 {
                color: $primary-color;
            }
            &__details {
                padding: 20px;
                background-color: $white;
                display: block;
                margin: 0;
                input {
                    width: 100%;
                    border-radius: 8px;
                    padding: 10px;
                    border: 1px solid #eee;
                }
                .button {
                    float: right;
                    background-color: $primary-color;
                    padding: 4px 0;
                    text-align: center;
                    margin-top: 10px;
                    &:hover {
                        background-color: transparent;
                        border: 1px solid $primary-color;
                        transition: all .4s ease;
                        a {
                            color: $primary-color;
                        }
                    }
                    a {
                        font-size: 12px;
                        line-height: 26px;
                        color: $white;
                    }
                    &.button-red {
                        border: 1px solid $red;
                        background-color: $red;
                        &:hover {
                            transition: all .4s ease;
                            background-color: transparent;
                            border: 1px solid $red;
                            a {
                                color: $red;
                            }
                        }
                    }
                }
            }
        }
    }
    .svg {
        height: 15px;
        width: 15px;
        padding-bottom: 3px;
        margin-right: 5px;
    }
    .sidebar {
        height: 100%;
        background-color: $white;
        &__logo {
            text-align: center;
            padding: 10px 0;
            img {
                width: 100px;
            }
        }
    }
}

// Side bar
.wrapper {
    display: flex;
    align-items: stretch;
}

#sidebar {
    min-width: 250px;
    max-width: 250px;
    min-height: 100vh;
}

#sidebar.active {
    margin-left: -250px;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
}

@media (max-width: 768px) {
    #sidebar {
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
}

body {
    background: #f5f5f5;
}

p {
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    color: #999;
}

a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

#sidebar {
    /* don't forget to add all the previously mentioned styles here too */
    background: $white;
    color: $text-color;
    transition: all 0.3s;
}

#sidebar .sidebar-header {
    padding: 30px 0;
    background: $white;
    text-align: center;
    img {
        width: 150px;
    }
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 14px;
    line-height: 22px;
    display: block;
}

#sidebar ul li a:hover {
    color: $white;
    background: $primary-color;
    .svg {
        filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(93deg) brightness(103%) contrast(103%);
    }
}

#sidebar ul li.active>a,
a[aria-expanded="true"] {
    color: $white;
    background: $primary-color;
    .svg {
        filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(93deg) brightness(103%) contrast(103%);
    }
}

ul ul a {
    font-size: 12px !important;
    line-height: 20px!important;
    padding-left: 30px !important;
    background: $background-color;
}

.wrapper {
    position: relative;
    #content {
        background: $background-color!important;
        min-width: 80%;
        &.active {
            min-width: calc(80% + 250px);
        }
        .bg-light {
            background-color: transparent!important;
        }
        #sidebarCollapse {
            background-color: transparent;
            color: $black;
            border: none;
            position: absolute;
            top: 30px;
            left: 10px;
        }
    }
}

.table-card {
    border: 0;
    background-color: transparent;
    margin: 50px auto;
    padding: 0 150px;
    .title {
        display: inline-flex;
        width: 100%;
        justify-content: space-between;
        h3 {
            color: $primary-color;
        }
        .product-category {
            background-color: $white;
            display: inline-flex;
            align-items: center;
            border-radius: 8px;
            margin-bottom: 20px;
            span {
                padding: 0 20px;
                color: $primary-color;
                font-weight: 600;
                &.category {
                    border-right: 1px solid $text-color;
                }
            }
            .dropdown {
                box-shadow: none!important;
                .title {
                    padding: 0 40px;
                }
            }
        }
    }
}

.table {
    background-color: $white;
    thead {
        th {
            color: $primary-color;
        }
    }
    tbody {
        tr {
            color: #959595;
            td {
                img {
                    height: 100px;
                    width: auto;
                }
                &.icons {
                    display: inline-flex;
                    .delete-icon,
                    .edit-icon {
                        color: $primary-color;
                        .svg {
                            cursor: pointer;
                        }
                    }
                }
            }
        }
    }
}


/*Styling Selectbox*/

.dropdown {
    width: 100%;
    display: inline-block;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 0 2px rgb(204, 204, 204);
    transition: all 0.5s ease;
    position: relative;
    font-size: 14px;
    color: #474747;
    height: 100%;
    text-align: left;
    .select {
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        padding: 10px;
        width: 100%;
        justify-content: space-between;
        span {
            font-size: 14px;
            line-height: 22px;
        }
        .svg {
            color: #888;
            cursor: pointer;
            transition: all 0.3s ease-in-out;
            float: right;
            line-height: 20px;
            padding: 0;
            height: 15px;
            width: 15px;
        }
    }
    &:hover {
        box-shadow: 0 0 4px rgb(204, 204, 204);
    }
    &:active {
        background-color: #f8f8f8;
    }
    &.select i {
        transform: rotate(-90deg);
    }
    &.active:hover,
    &.dropdown.active {
        box-shadow: 0 0 4px rgb(204, 204, 204);
        border-radius: 5px 5px 0 0;
        background-color: #f8f8f8;
    }
    .dropdown-menu {
        position: absolute;
        background-color: #fff;
        width: 100%;
        left: 0;
        margin-top: 1px;
        box-shadow: 0 1px 2px rgb(204, 204, 204);
        border-radius: 0 1px 5px 5px;
        overflow: hidden;
        display: none;
        max-height: 144px;
        overflow-y: auto;
        z-index: 9;
        padding: 0;
        list-style: none;
        li {
            padding: 10px;
            font-size: 14px;
            line-height: 22px;
            transition: all 0.2s ease-in-out;
            cursor: pointer;
            &:hover {
                background-color: #f2f2f2;
            }
            &:active {
                background-color: #e2e2e2;
            }
        }
    }
}