.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: $background-color;
    min-height: 93vh;
    height: 100%;
    // padding: 176px 0;
    .logo-container {
        text-align: center;
        .logo {
            width: 200px;
            margin-bottom: 50px;
        }
    }
    .card {
        background-color: $white;
        margin: 0 auto 100px;
        text-align: center;
        padding: 20px 100px;
        box-shadow: 0px 7px 6px -4px rgba(0, 0, 0, 0.69);
        h3 {
            color: $primary-color;
            margin: 20px 0;
        }
        input {
            border-radius: 8px;
            color: $text-color;
            padding: 6px 10px;
            margin: 6px 0;
            width: 100%;
            border: 1px solid #eee;
        }
        .primary-button {
            margin: 20px 0;
            cursor: pointer;
            a {
                background-color: $primary-color;
                border: 1px solid $primary-color;
                color: $white;
                padding: 10px 30px;
                &:hover {
                    transition: all .4s ease;
                    color: $primary-color;
                    background-color: transparent;
                }
            }
        }
    }
}

.footer-login {
    background-color: $grey;
}