/*Styling Selectbox*/
.dropdown {
  width: 100%;
  display: inline-block;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 2px #cccccc;
  transition: all 0.5s ease;
  position: relative;
  font-size: 14px;
  color: #474747;
  height: 100%;
  text-align: left;
}

.dropdown .select {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  padding: 10px;
  width: 100%;
  justify-content: space-between;
}

.dropdown .select span {
  font-size: 14px;
  line-height: 22px;
}

.dropdown .select .svg {
  color: #888;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  float: right;
  line-height: 20px;
  padding: 0;
  height: 15px;
  width: 15px;
}

.dropdown:hover {
  box-shadow: 0 0 4px #cccccc;
}

.dropdown:active {
  background-color: #f8f8f8;
}

.dropdown.select i {
  transform: rotate(-90deg);
}

.dropdown.active:hover, .dropdown.dropdown.active {
  box-shadow: 0 0 4px #cccccc;
  border-radius: 5px 5px 0 0;
  background-color: #f8f8f8;
}

.dropdown .dropdown-menu {
  position: absolute;
  background-color: #fff;
  width: 100%;
  left: 0;
  margin-top: 1px;
  box-shadow: 0 1px 2px #cccccc;
  border-radius: 0 1px 5px 5px;
  overflow: hidden;
  display: none;
  max-height: 144px;
  overflow-y: auto;
  z-index: 9;
  padding: 0;
  list-style: none;
}

.dropdown .dropdown-menu li {
  padding: 10px;
  font-size: 14px;
  line-height: 22px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}

.dropdown .dropdown-menu li:hover {
  background-color: #f2f2f2;
}

.dropdown .dropdown-menu li:active {
  background-color: #e2e2e2;
}

