.nav {
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    position: relative;
    background: #43a6c6;
    .image {
        opacity: 0.6;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
    .row {
        margin: 0;
    }
    .banner {
        padding: 100px 0;
        @include mq('phablet', max) {
            padding: 80px 0;
            transition: all .8s ease;
        }
        @include mq('phablet', max) {
            padding: 60px 0;
            transition: all .8s ease;
        }
        &__details {
            margin: 20px 0;
        }
    }
    .arrow-down {
        width: 100%;
        text-align: center;
        margin-bottom: 20px;
        cursor: pointer;
        z-index: 1;
        .svg {
            height: 40px;
            width: 40px;
        }
    }
}

.header {
    width: 100%;
    z-index: 3;
    background-color: transparent;
    padding: 20px 0;
    img {
        height: 50px;
        width: auto;
        margin-top: 10px;
    }
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        overflow: hidden;
        li {
            a {
                display: block;
                text-decoration: none;
                color: #fff;
                &:hover {
                    transition: all .2s ease;
                    color: $primary-color;
                }
            }
        }
    }
    .logo {
        display: block;
        float: left;
        font-size: 2em;
        padding: 10px 20px;
        text-decoration: none;
    }
    .menu {
        clear: both;
        max-height: 0;
        transition: max-height 0.2s ease-out;
    }
    .menu-icon {
        cursor: pointer;
        float: right;
        padding: 28px 20px;
        position: relative;
        user-select: none;
        .navicon {
            background: #fff;
            display: block;
            height: 2px;
            position: relative;
            transition: background 0.2s ease-out;
            width: 18px;
            &:before,
            &:after {
                background: #fff;
                content: "";
                display: block;
                height: 100%;
                position: absolute;
                transition: all 0.2s ease-out;
                width: 100%;
            }
            &:before {
                top: 5px;
            }
            &:after {
                top: -5px;
            }
        }
    }
}

.header .menu-btn {
    display: none;
}

.header .menu-btn:checked~.menu {
    max-height: 240px;
}

.header .menu-btn:checked~.menu-icon .navicon {
    background: transparent;
}

.header .menu-btn:checked~.menu-icon .navicon:before {
    transform: rotate(-45deg);
}

.header .menu-btn:checked~.menu-icon .navicon:after {
    transform: rotate(45deg);
}

.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:after {
    top: 0;
}


/* 48em = 768px */

@media (min-width: 48em) {
    .header li {
        float: left;
        padding: 20px;
    }
    .header .menu {
        clear: none;
        float: right;
        max-height: none;
    }
    .header .menu-icon {
        display: none;
    }
}

.banner {
    padding-top: 200px;
    &__title {
        display: block;
    }
    &__details {
        display: block;
        margin: 30px 0;
    }
    a {
        font-size: 18px;
        line-height: 34px;
        padding: 10px 80px;
        background-color: $primary-color;
        border: 1px solid $primary-color;
        color: $white;
        &:hover {
            text-decoration: none;
            background-color: transparent;
            color: $white;
            border: 1px solid $white;
            transition: all .8s ease;
        }
    }
}

// inner pages banner styles
.inner-page {
    .banner {
        padding: 60px 0 40px;
        @include mq('phablet', max) {
            padding: 50px 0 30px;
            transition: all .8s ease;
        }
    }
}