body {
    font-family: $default-font !important;
}

.primary-button {
    color: $white;
    border: none;
    list-style: none;
    text-decoration: none;
}

h1 {
    font-size: 64px;
    line-height: 72px;
    font-weight: bold;
    padding: 20px 0;
}

p {
    font-size: 14px;
    line-height: 22px;
}

.ecosystem {
    background-color: $background-color;
    padding: 40px 0;
    text-align: center;
    h1 {
        padding: 20px 0;
        color: $primary-color;
    }
    &__cards {
        padding: 20px 0;
        img {
            border-radius: 50%;
            width: 150px;
            height: 150px;
        }
        &--title {
            font-size: 24px;
            line-height: 32px;
            color: $primary-color;
            font-weight: bold;
            padding: 20px 0;
        }
        p {
            font-size: 14px;
            line-height: 22px;
            color: $text-color;
        }
    }
}

.appreciation {
    text-align: center;
    // background-image: linear-gradient( rgba(2, 131, 137, 0.4), rgba(2, 131, 137, 0.4)), url("https://images.pexels.com/photos/1720186/pexels-photo-1720186.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500");
    background-repeat: no-repeat;
    background-size: cover;
    color: $white;
    padding: 40px 0;
    position: relative;
    background: $primary-color;
    .image {
        opacity: 0.6;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
    h1 {
        color: $white;
        padding: 20px 0;
        @include mq('phablet', max) {
            color: $primary-color;
        }
    }
    p {
        color: $white;
        font-size: 16px;
        line-height: 24px;
    }
    &__slider {
        .container {
            width: 700px;
        }
        img {
            border-radius: 50%;
            height: 150px;
            width: 150px;
        }
        &--img {
            display: inline-flex;
            justify-content: left;
            padding: 20px;
            @include mq('phablet', max) {
                justify-content: center;
            }
        }
        &--info {
            text-align: left;
            align-items: center;
            padding: 20px 0;
            @include mq('phablet', max) {
                justify-content: center;
                text-align: center;
                width: 100%;
            }
            h3 {
                @include mq('phablet', max) {
                    color: $primary-color;
                }
            }
        }
        &--details {
            text-align: left;
            padding: 50px 0;
            @include mq('phablet', max) {
                justify-content: center;
                text-align: center;
                width: 100%;
                ;
                padding: 20px
            }
        }
    }
    .slick {
        position: relative;
        .fas {
            font-size: 30px;
        }
        .fa-chevron-left {
            left: 10px;
            position: absolute;
            top: 40%;
        }
        .fa-chevron-right {
            right: 10px;
            position: absolute;
            top: 40%;
        }
        .slick-dots {
            width: 100%;
            li {
                height: 15px;
                width: 15px;
                border-radius: 50%;
                ;
                background-color: #d9d9d9;
                button {
                    display: none;
                }
                &.slick-active {
                    background-color: $primary-color;
                }
            }
        }
    }
    .slick-track {
        .container {
            width: 100%;
            .row {
                width: 100%;
            }
        }
    }
}

.container {
    width: 100% !important;
    .row {
        margin: 0;
        width: 100% !important;
    }
}

.form {
    background-color: $background-color;
    text-align: center;
    padding: 40px 0;
    h1 {
        padding: 20px 0;
        color: $primary-color;
    }
    .container {
        width: 700px!important;
        max-width: 100%;
    }
    text-align:center;
    input,
    textarea {
        padding: 6px 10px;
        border-radius: 8px;
        margin: 5px 0;
        border: 1px solid #eee;
        background-color: #fff;
        color: $text-color;
        width: 100%;
    }
    .button {
        text-align: center;
        margin-top: 50px;
        a {
            text-decoration: none;
            font-size: 18px;
            line-height: 34px;
            padding: 10px 80px;
            background-color: $primary-color;
            border: 1px solid $primary-color;
            color: $white;
            &:hover {
                text-decoration: none;
                background-color: transparent;
                color: $primary-color;
                border: 1px solid $primary-color;
                transition: all .8s ease;
            }
        }
    }
}

.filter {
    background-color: $background-color;
    padding: 0 0 40px;
    .links {
        text-align: center;
        padding: 40px 0;
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            border-radius: 50%;
            margin: 0;
            padding: 0;
            li {
                list-style: none;
                text-decoration: none;
                padding: 5px 10px;
                a {
                    color: $text-color;
                    padding: 0px 15px 2px;
                    font-weight: bold;
                    &.active,
                    &:hover {
                        text-decoration: none;
                        background-color: $primary-color;
                        color: $white;
                        border-radius: 20px;
                    }
                }
            }
        }
    }
    .cards {
        .cards--items {
            p {
                background-color: $white;
                border-radius: 4px;
                font-weight: bold;
                margin: 0;
                padding: 10px;
                font-size: 14px;
                line-height: 22px;
                border: 4px 4px 0 0;
                color: $text-color;
            }
        }
        img {
            height: 100%;
            width: 100%;
            border: 0 0 4px 4px;
        }
    }
}

// contact page styles
.contact-details {
    padding: 40px 0;
    text-align: center;
    h1 {
        color: $primary-color;
    }
    &__info {
        text-align: left;
        .social-media {
            display: block;
            float: left;
            p {
                display: inline;
                margin-left: 10px;
                font-size: 14px;
                line-height: 22px;
                color: $text-color;
            }
            i {
                margin: 0;
            }
        }
    }
}

.contact-details {
    .container {
        width: 800px;
        iframe {
            width: 100%;
            height: auto;
        }
    }
}